:root {
    --text-dark-color: #111111;
    --text-light-color: #ffffff;
    --text-orange-color: #fd9222;
    --text-dark-transparent-color: #111111b3;
    --text-light-transparent-color: #ffffffb3;
    --text-label-dark-transparent-color: #4040404d;
    --icon-dark-color: #111111;
    --icon-dark-transparent-color: #1111111a;
    --icon-orange-color: #fd9222;
    --background-dark-color: #1e1823;
    --background-light-color: #fafafa;
    --background-white-color: #ffffff;
    --background-orange-color: #fd9222;
    --background-button-color: #fcf1e0;
    --background-modal-light-color: #fafafa;
    --border-dark-transparent-color: #ffffff80;

    --transition-duration: 250ms;
    --transition-tf: cubic-bezier(0.4, 0, 0.2, 1);
}

.section {
    padding-top: 64px;
    padding-bottom: 64px;
}

.top_sellers_section {
    padding-top: 36px;
    padding-bottom: 36px;
}

.top_seller_conteiner {
    max-width: 375px;
    padding-left: 20px;
    padding-right: 20px;
    margin: 0 auto;
    align-items: center;
}

.top_sellers_title {
    font-weight: 600;
    font-size: 20px;
    line-height: 1.2;

    text-align: center;
    letter-spacing: -0.03em;
    text-transform: uppercase;
    color: var(--text-dark-color);
    display: block;
    margin-bottom: 32px;
}

.top_seller_accent {
    color: var(--text-orange-color);
}


.top_sellers_list {
    flex-wrap: wrap;
    justify-content: center;
    padding-left: 0px;
}

.top_sellers_item {
    display: flex;
}

.top_sellers_img {
    width: 100%;
    border-radius: 15px;
}

.top_sellers_item_only_pc {
    display: none;
}

.top_seller_text {
    max-width: 306px;
    margin-top: 20px;
    margin-bottom: 32px;

    font-size: 14px;
    line-height: 1.29;
    letter-spacing: -0.02em;
}

.top_seller_paragraf {
    display: none;
    font-size: 14px;
    line-height: 1.29;
    letter-spacing: -0.02em;
    margin: 0px;
}

.top_sellers_bottom_info {
    display: flex;
    margin-top: 20px;
    justify-content: center;
}

.top_seller_button {
    min-width: 120px;
    display: block;
    padding: 12px 28px;
    border: none;
    border-radius: 100px;
    background-color: var(--text-orange-color);
    color: var(--text-light-color);

    font-weight: 600;
    font-size: 14px;
    line-height: 1.21;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
        background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);;
}

.top_seller_button:hover,
.top_seller_button:focus {
    background-color: var(--background-button-color);
    color: var(--background-orange-color);
}


@media screen and (min-width: 768px) {
    .top_sellers_section {
        padding-top: 50px;
        padding-bottom: 50px;
    }

    .top_seller_conteiner {
        max-width: 768px;
        padding-left: 32px;
        padding-right: 32px;
    }

    .top_sellers_title {
        margin-bottom: 50px;
        font-size: 24px;
        line-height: 1.17;

    }

    .top_sellers_list {
        display: flex;
        gap: 16px;
    }

    .top_sellers_item {
        flex-basis: calc((100% - 16px)/2);
    }

    .top_seller_text {
        display: none;
        margin-bottom: 0px;
    }

    .top_seller_paragraf {
        display: flex;
        max-width: 306px;
    }

    .top_seller_button {
        min-width: 162px;
        padding: 14px 40px;
    }

    .top_sellers_bottom_info {
        display: flex;
        margin-top: 32px;
        justify-content: space-between;
        align-items: flex-start;
    }
}

@media screen and (min-width: 1200px) {
    .top_sellers_section {
        padding-top: 64px;
        padding-bottom: 64px;
        ;
    }

    .top_seller_conteiner {
        max-width: 1200px;
    }

    .top_sellers_list {
        flex-wrap: nowrap;
    }

    .top_sellers_item {
        flex-basis: calc((100% - 32px)/3);
    }

    .top_seller_paragraf {
        display: flex;
        max-width: 407px;
    }

    .top_sellers_item_only_pc {
        display: flex;
    }
}