body {
  color: var(--text-dark-color);
  background-color: var(--background-light-color);
  font-family: Montserrat, sans-serif;
}

:root {
  --text-dark-color: #111;
  --text-light-color: #fff;
  --text-orange-color: #fd9222;
  --text-dark-transparent-color: #111111b3;
  --text-light-transparent-color: #ffffffb3;
  --text-label-dark-transparent-color: #4040404d;
  --icon-dark-color: #111;
  --icon-dark-transparent-color: #1111111a;
  --icon-orange-color: #fd9222;
  --background-dark-color: #1e1823;
  --background-light-color: #fafafa;
  --background-white-color: #fff;
  --background-orange-color: #fd9222;
  --background-button-color: #fcf1e0;
  --background-modal-light-color: #fafafa;
  --border-dark-transparent-color: #ffffff80;
  --transition-duration: .25s;
  --transition-tf: cubic-bezier(.4, 0, .2, 1);
}

* {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  display: block;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0;
}

:root {
  --text-dark-color: #111;
  --text-light-color: #fff;
  --text-orange-color: #fd9222;
  --text-dark-transparent-color: #111111b3;
  --text-light-transparent-color: #ffffffb3;
  --text-label-dark-transparent-color: #4040404d;
  --icon-dark-color: #111;
  --icon-dark-transparent-color: #1111111a;
  --icon-orange-color: #fd9222;
  --background-dark-color: #1e1823;
  --background-light-color: #fafafa;
  --background-white-color: #fff;
  --background-orange-color: #fd9222;
  --background-button-color: #fcf1e0;
  --background-modal-light-color: #fafafa;
  --border-dark-transparent-color: #ffffff80;
  --transition-duration: .25s;
  --transition-tf: cubic-bezier(.4, 0, .2, 1);
}

.header-container {
  max-width: 335px;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
}

.header-logo {
  justify-content: flex-start;
  align-items: center;
  gap: 4px;
  display: inline-flex;
}

.header-navigation {
  justify-content: space-around;
  align-items: center;
  gap: 138px;
  padding: 20px 0 14px;
  display: flex;
}

.header-logo-link {
  color: #111;
  letter-spacing: -.03em;
  text-transform: uppercase;
  align-items: center;
  font-family: Montserrat;
  font-size: 14px;
  font-style: italic;
  font-weight: 700;
  line-height: 1.3;
  text-decoration: none;
  display: flex;
}

.header-logo-choco {
  padding: 0 0 0 4px;
  font-style: normal;
}

.header-hamburger {
  align-items: center;
  margin: 0 0 0 auto;
}

.header-hamburger:hover, .header-hamburger:focus {
  cursor: pointer;
  fill: var(--pressed-state);
  fill: currentColor;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 3px 1px #0000001a, 0 2px 1px #00000014, 0 2px 2px #0000001f;
}

.menu-toggle {
  background-color: #0000;
  border: none;
  margin: 0;
  padding: 0;
}

.menu-container {
  width: 220px;
  height: 100vh;
  background-color: var(--background-dark-color);
  z-index: 999;
  flex-direction: column;
  justify-content: space-between;
  padding: 40px;
  transition: transform .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.close-menu {
  color: var(--background-white-color);
  cursor: pointer;
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), border .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 20px;
  right: 20px;
}

.close-icon {
  color: var(--icon-orange-color);
  width: 28px;
  height: 28px;
  transition-property: width, height;
  transition-duration: .25s;
}

.header-mobile-navigation {
  padding: 0;
  position: absolute;
  top: 136px;
  left: 50px;
}

.header-mobile-nav-list {
  flex-direction: column;
  gap: 24px;
  margin: 0;
  list-style: none;
  display: flex;
}

.header-nav-li-mobile {
  text-decoration: none;
}

.header-nav-link-menu {
  color: var(--background-white-color);
  letter-spacing: .02em;
  padding-top: 40px;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
}

.header-nav-link-menu:hover, .header-nav-link-menu:focus {
  color: #fd9222;
}

.header-link-underline {
  color: #fd9222;
  transition-property: color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.header-link-underline:after {
  content: "";
  height: 1px;
  background-color: var(--background-orange-color);
  width: 0%;
  border-bottom: 2px;
  border-radius: 2px;
  transition-property: width;
  transition-timing-function: linear;
  display: block;
  position: absolute;
  bottom: -8px;
  left: 0;
}

.header-sm-mobile {
  align-items: center;
  gap: 18px;
  display: flex;
}

.header-mobile-sm-list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.header-sm-mobile-link {
  width: 100%;
  height: 100%;
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.header-sm-mobile-link:hover, .header-sm-mobile-link:focus {
  color: #fd9222;
  cursor: pointer;
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-container .header-mobile-nav-list {
  max-width: 200px;
  justify-content: start;
  gap: 0;
}

.menu-container {
  margin-bottom: 48px;
  font-size: 20px;
  font-weight: 500;
}

.header-mobile-sm-list {
  gap: 56px;
  display: flex;
}

.address-link-mobile {
  flex-direction: column;
  gap: 40px;
  margin-bottom: 48px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header-container {
    max-width: 704px;
    align-items: center;
  }

  .header-navigation {
    gap: 503px;
    padding: 24px 0;
  }

  .menu-container {
    width: 290px;
  }
}

@media screen and (max-width: 1157px) {
